import React from 'react';
import { NavLink } from 'react-router-dom';

//Componente
//Estado
//Propriedade

function SideBar() {
  return <aside>
    <div className="wrapper">
      <div className="sidebar">
        <h2>Eplan Tools</h2>
        <ul>
          <li className="fas fa-home">
            <NavLink to="/home" >Home </NavLink>
          </li>
          <li className="fas fa-project-diagram">
            <NavLink to="/eplantools" >Eplan to Excel </NavLink>
          </li>
           <li className="fas fa-project-diagram">
            <NavLink to="/compareDI" >Compara DI </NavLink>
          </li>
           <li className="fas fa-project-diagram">
            <NavLink to="/compareLC" >Compara LC </NavLink>
          </li>
          <li className="fas fa-address-card">
            <NavLink to="/about" >About </NavLink>
          </li>
        </ul>
      </div>
    </div>
  </aside>
}

export default SideBar;