import React, { useState } from 'react';
import api from '../services/api'
import Axios from 'axios';
import fileDownload from 'js-file-download';
import SideBar from './SideBar';
import Report from './ReportBar'


function CompareLC() {
  async function viewHandler() {
    Axios(`/downloadLC`, {
      method: "GET",
      responseType: "blob",
    })
      .then(async response => {
        const file2 = await new Blob([response.data], {
          type: "application/downloadLC"
        });
        fileDownload(file2, 'comparacaoLC.xlsx');
        const file2URL = URL.createObjectURL(file2);
        window.open(file2URL);
        var uploadConcluded = document.querySelector('#uploadLoading')
        uploadConcluded.textContent = '';
      })
      .catch(error => {
        console.log(error);
      });
  }

  const [LCOld, setOldLC] = useState({
    file: null,
    

  });
    const [LCNew, setNewLC] = useState({
    file: null,
  });

  async function onClickHandler(e, uploaded) {
    var uploadConcluded = document.querySelector('#uploadLoading')
    uploadConcluded.textContent = 'Uploading...'
    const url = `/comparaLC`;
    const formData = new FormData();
    
    
    
   

    formData.append('file', LCOld)
    const config = {
      headers: {
        'content-type': 'multipart/form-data'
      },
    }
    formData.append('file', LCNew)
    

    console.log(formData)

    api.post(url, formData, config)
      .then(function (response) {
      })
      .finally(() => {
        var uploadConcluded = document.querySelector('#uploadLoading')
        uploadConcluded.textContent = 'Done!Ready for Download.'
      })
      .catch(() => {
        var uploadConcluded = document.querySelector('#uploadLoading')
        uploadConcluded.textContent = 'Upload Error.'
      })
      ;

    e.preventDefault();
  }
  return (
    <>
      <SideBar />
      <div className="wrapper">
        <div className="main_content">
          <div className="header">Compara LC.</div>
          <img className="logos" src={require('../assets/excel.png')} alt="logos"/>
          <div className="container">
            <form>
              <input onChange={e => setOldLC(e.target.files[0])} type='file' name='files' required ></input>
              <p className="arquivoOrigem">Upload da versão <b>Anterior</b> em Excel</p>
            </form>
          </div>
            <img className="logos" src={require('../assets/excel.png')} alt="logos"/>
          <div className="container">
            <form>
              <input onChange={e => setNewLC(e.target.files[0])} type='file' name='files' required ></input>
              <p className="arquivoOrigem">Upload da versão <b>Nova</b> em Excel</p>
            </form>
          </div>
          
          <div className="containerC">
            <img className="logoConverte" src={require("../assets/convert.png")} alt="logoConverte"/>
            <button onClick={onClickHandler} className="converterxml" htmlFor="converter">Click to Upload </button>
            <p id="uploadLoading"></p>
          </div>
          <div className="containerB">
            <img className="logoSave" src={require("../assets/SAVE.png")} alt="logoSave"/>
            <button onClick={viewHandler} className="saveExcel" >Click to Download </button>
            <p id="carregamento"></p>
          </div>
          <Report/>
        </div>
      </div>
    </>
  )
}

export default CompareLC;