import React from 'react';

import {BrowserRouter, Route, Switch, Redirect} from 'react-router-dom'
import Home from './components/Home';
import EplanTools from './components/EplanTools';
import About from './components/About'
import Report from './components/ReportBar'
import CompareDI from './components/CompareDI'
import CompareLC from './components/CompareLC'

const Routes = () => (
    <BrowserRouter>
            <Switch>
            <Route  exact path="/" Redirect to = "/eplantools" component={EplanTools}/>
            <Route  exact path="/home" component={Home}/>
            <Route  path="/eplantools" component={EplanTools}/>
            <Route  path="/about" component={About}/>
            <Route  path="/compareDI" component={CompareDI}/>
            <Route  path="/compareLC" component={CompareLC}/>
            </Switch>
    </BrowserRouter>
)


export default Routes;