import React, { useState } from 'react';
import api from '../services/api'
import Axios from 'axios';
import fileDownload from 'js-file-download';
import SideBar from './SideBar';
import Report from './ReportBar'
//Componente
//Estado
//Propriedade

function EplanTools() {
  async function viewHandler() {
    Axios(`/download`, {
      method: "GET",
      responseType: "blob",
    })
      .then(async response => {
        const file = await new Blob([response.data], {
          type: "application/download"
        });
        fileDownload(file, 'interligacao.xlsx');
        const fileURL = URL.createObjectURL(file);
        window.open(fileURL);
        var uploadConcluded = document.querySelector('#uploadLoading')
        uploadConcluded.textContent = '';
      })
      .catch(error => {
        console.log(error);
      });
  }

  const [xml, setXML] = useState({
    file: null,
  });

 
  async function clearHandler(){
    var relatorio = document.querySelector('#reportArea')
    relatorio.value = '' 
  }
  async function onClickHandler(e, uploaded) {
    var uploadConcluded = document.querySelector('#uploadLoading')
    uploadConcluded.textContent = 'Uploading...'
    const url = `/xml`;
    const formData = new FormData();
    formData.append('file', xml)
    const config = {
      headers: {
        'content-type': 'multipart/form-data'
      },
        // `maxContentLength` defines the max size of the http response content in bytes allowed
  maxContentLength: 20000,

  // `maxBodyLength` (Node only option) defines the max size of the http request content in bytes allowed
  maxBodyLength: 20000,
    }

    api.post(url, formData, config)
      .then(function (response) {
        var relatorio = document.querySelector('#reportArea')
        
        var valor = ''
        for(var n=0; n < response.data.length; n++){
          
          var problemas = ''
         var testes;
         var tete;
         
          for(var v=0; v< response.data[n].Problemas.length; v++){
            problemas = [problemas + response.data[n].Problemas[v] + ', ']
          }
        
         
       
          valor = [valor + response.data[n].TagCabo + ': ' +  problemas + '\n']
          valor[valor.length-1] = valor[valor.length-1].slice(0,-3)
          valor = [valor + '\n']
       
        }
        relatorio.value = valor 
      })
      .finally(() => {
        var uploadConcluded = document.querySelector('#uploadLoading')
        uploadConcluded.textContent = 'Done!Ready for Download.'
        
      })
      .catch(() => {
        var uploadConcluded = document.querySelector('#uploadLoading')
        uploadConcluded.textContent = 'Upload Error.'

      })
      ;

    e.preventDefault();
  }
  return (
    <>
      <SideBar />
      <div className="wrapper">
        <div className="main_content">
          <div className="header">Welcome!! Have a nice day.</div>
          <img className="logos" src={require('../assets/eplan.png')} alt="logos"/>
          <div className="container">
            <form>
              <input onChange={e => setXML(e.target.files[0])} type='file' name='files' required ></input>
              <p className="arquivoOrigem">Upload your XML from Eplan</p>
            </form>
          </div>
          <div className="containerC">
            <img className="logoConverte" src={require("../assets/convert.png")} alt="logoConverte"/>
            <button onClick={onClickHandler} className="converterxml" htmlFor="converter">Click to Upload </button>
            <p id="uploadLoading"></p>
          </div>
          <div className="containerB">
            <img className="logoSave" src={require("../assets/SAVE.png")} alt="logoSave"/>
            <button onClick={viewHandler} className="saveExcel" >Click to Download </button>
            <p id="carregamento"></p>
          </div>
          <div className = 'containerReport'>
        <div className="header2">Relatório de Erros
        <button onClick={clearHandler} className="clear" >Clear</button>
        </div>
        <textarea id="reportArea" name="Report">Veja aqui o Relatório de Erros ao importar o arquivo</textarea>
        
        
    </div>
        </div>
      </div>
    </>
  )
}

export default EplanTools;