import React from 'react';
import SideBar from './SideBar';
import Axios from 'axios';
import fileDownload from 'js-file-download';


//Componente
//Estado
//Propriedade

function Home() {

  async function viewHandlerL() {
    Axios(`/ModeloLC`, {
      method: "GET",
      responseType: "blob",
    })
      .then(async response => {
        const file2 = await new Blob([response.data], {
          type: "application/ModeloLC"
        });
        fileDownload(file2, 'ModeloLC.xlsx');
        const file2URL = URL.createObjectURL(file2);
        window.open(file2URL);
    
      })
      .catch(error => {
        console.log(error);
      });
  }

  async function viewHandlerD() {
    Axios(`/ModeloDI`, {
      method: "GET",
      responseType: "blob",
    })
      .then(async response => {
        const file2 = await new Blob([response.data], {
          type: "application/ModeloDI"
        });
        fileDownload(file2, 'ModeloDI.xlsx');
        const file2URL = URL.createObjectURL(file2);
        window.open(file2URL);
    
      })
      .catch(error => {
        console.log(error);
      });
  }
  return (
    <>
      <SideBar />
      <div className="wrapper">
        <div className="main_content">
          <div className="header">Getting started!.</div>
          <p className="arquivoOrigem">
            Essa Aplicação irá ajudar você transformar o diagrama de interligação exportado do EPLAN para um formato excel.
          </p>
          <p className="arquivoOrigem">
            Antes de iniciar, tenha certeza que você possui o arquivo .xml exportado corretamente.
          </p>
          <p className="arquivoOrigem">
            Em caso de duvidas, problemas ou sugestões, favor entrar em contato (brds@intertechne.com.br) 
          </p>

          <button onClick={viewHandlerD} className="arquivoOrigem">Download Modelo de Importação para comparação DI</button>
          <button onClick={viewHandlerL} className="arquivoOrigem">Download Modelo de Importação para comparação LC</button>
        </div>
      </div>
    </>
  )
}

export default Home;